body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.detail { background-color: white;width: 65%;margin:auto;margin-top: 2%;position: relative;  } 
    .detail a{ padding: 5px;border-radius: 2px; }
      .detail p{ font-size: 2.0em;padding-left: 2%;letter-spacing: -.05rem;padding-top: 30px;text-align: center; }   
      .htd{ padding-top: 1%;padding-bottom:1%;float: left; }  .dtd{ padding-left: 10%; }  
      .back { position: absolute;top:5px;left: 5px; background-color: slategray;color: white;font-size: .7em; }
      .delete { position: absolute;top:5px;right: 5px; background-color: slategray;color: white;font-size: .7em;background-color: rgba(177, 101, 71, 0.849);  }
      .withdraw { position: absolute;bottom:5px;right: 15%; background-color: slategray;color: white;font-size: 1.3em ;padding-left: 1%;background-color: rgba(177, 101, 71, 0.849); } 
      .deposit { position: absolute;bottom:5px;right: 1%; background-color: slategray;color: white;font-size: 1.3em;background-color: slategray;  } 
     
.withdraw_c   { background-color: white;width: 45%;margin: auto;margin-top: 1%;min-height: 250px;padding-bottom: 2%;}   
      .withdraw_c div{ background-color: slategray;line-height: 4.5em; }
      .withdraw_c div p{ font-size: 2.1em;color: white;text-align: center;  }
      .withdraw_c span{ display: inline-block;padding-left: 1%;width: 80px;margin-top: 20px; }
      .withdraw_c span a{ margin-top: 20px;padding: 3px;background-color: slategray;color: white;margin-left: 10px;border-radius: 3px }
      .withdraw_c input{ width: 60% ;float: right;line-height: 1.5em;margin-right: 10%;margin-top: 20px;outline: none;border: none;border-bottom: 1px dashed slategray; }
    



      


body  { background-color: #ccc; }

  a { text-decoration: none; }    
  .header { background-color: steelblue;line-height: 3.4em;color: white; }
        .header p  {  padding-left:15%; font-size: xx-large ;display: inline;}
        .header ul { list-style-type:none; display: inline;padding-left: 25% }
        .header ul li{ display: inline;padding-left: 2%; }
        .header ul li a { text-decoration: none;color: white;  }

  .dashboard { width: 65%;margin-left: auto;margin-right: auto; display: flex;margin-top: 2%; }
        .dleft { min-height: 220px;width:45%;float: left;background-color: white;margin-right: 2%;border-radius: 5px;  }
        .top { min-height: 20% ;position: relative; } hr{ width: 95%;}
        .top span { font-size: x-large;padding-bottom: 80%;  }
        .dlinks { display: flex; float: right;position: absolute;right: 0;top:0;padding:5px;padding-top: 15px;}
        .dlinks a{padding: 4px 5px; margin-left: 5px; border-radius: 2px; text-decoration: none;font-size: .8em;background-color: slategray;color: white; }
       .bottom { padding-top: 40px;  }
       .bottom a{ text-decoration: none;font-size: 1.4em;color: #665; }
        .dright { min-height: 220px;width:45%;float: right;background-color: white;margin-left: 2%;border-radius: 6px;   }
        .dicon { height: 30px;margin: 2% 1% 0 2%; }

  .create { background-color: white;width: 65%;margin: auto;min-height: 450px;border-radius: 5px;position: relative; }
        .create h1{ background-color: slategray;text-align: center;padding: 2%;border-top-right-radius: 5px;border-top-left-radius: 5px;font-weight: lighter;color: white; }
        .create span { padding: 2%;float: left;width: 20%;font-size: .9em  }
        .create input,.create a{ float: left;width: 70%;margin: 2%;font-size: 1em; border: none;border-bottom: 1px dashed slategray;outline: none;}
        .create input[type='submit'],.create a{ width: 20%;border: none;font-size: .8em;padding: 10px;background-color: slategray;color: white;font-size: 0.8em;border-radius: 10px;}
        .create input[type='submit']:hover { cursor: pointer;  }
        .create a { text-decoration: none;text-align: center;    }
        .create select { margin: 2%;font-size: .8em;width: 20%;padding: 1%; border-width: thin;border-radius: 5px; outline: none;}
        hr{ width: 100%;border-top: 1px solid #ccc; }

  .accounts,.transactions {  width: 65%;min-height: 300px;background-color: white;margin: auto;margin-top: 2%;border-radius: 5px;position: relative;    }
        .goback,.gobackfromt { padding: 3px; position: absolute;top: 0;left: 0;margin-top: 10px;margin-left: 10px;color: white;border-radius: 2px; text-decoration: none;background-color: slategrey;font-size: .8em;} 
        .addnew { padding: 4px;position: absolute;top: 0;right: 0;margin-right: 10px;margin-top: 10px;color: white;border-radius: 2px;text-decoration: none;background-color: slategrey;font-size: .8em; }
        .accounts span,.transactions span { font-size: larger; } 
        .innerdivA,.innerdivT {   width: 100%;text-align: center;min-height: 50px; border-bottom: 1px solid #ccc; }
        .innerdivA img ,.innerdivT img { padding-top: 70px;height: 20px;margin-right: 10px;  } .innerdiv span {  font-size: 1.8em;  }
        table   { width: 100%;overflow: auto;margin-top: 0px}
        .accTable {   } .accTable tr th{ font-family: 'Courier New', Courier, monospace;background-color: slategray;height: 40px;color: white; }
        table tr {   width: inherit; text-align: left;border:1px dashed #ccc   } .accTable tr th, .accTable tr td{ text-align: center;width: 20%;border:1px solid #ccc;font-size: .9em;  }
         
  .footer { text-align: center;margin-top: 10px;font-size: .8em; }
